.sidebar{
    align-items: center;
    gap: 5px;
    color: #ffff;
    
}
.sidebar h1{
    border-bottom: 2px solid black;
    width: 100%;
}
.btn{
    text-align: center;
    padding: 5px;
    background: #ffffff36;
    width: 60%;
    color: #ffff;
    border-radius: 10px;
}
form{
    display: flex;
    flex-direction: column;
    margin: auto;
    border: 2px solid black;
    gap: 10px;
    padding: 50px;
    width: 20%;
  
}

form input {
    height: 40px;
    padding-left: 10px;
}
form button{
    background: royalblue;
    cursor: pointer;
    color: #ffff;
    height: 40px;
}

